import search from '../../components/my-components/SearchInput'
import shopBlock from '../components/SelectShop--Item.vue'
import longButton from '../../components/my-components/LongButton.vue'
import getShops from '../components/getShopList.js'
let DataFrom = {};
let DataGo = {};
let pageId = 'selectShop';
let ajaxIndex = 1; //当前是第几次请求 
let requesting = false;
export default {
    components: {
        search,
        shopBlock,
        longButton
    },
    data() {
        return {
            searchShop: '',
            shops: [],
            shopNum: 0,
            totalShop: -1,
            requesting: false //是否正在请求
        }
    },
    methods: {
        selectShop(item) {
            item.totalShops=this.totalShop;
            this.Cacher.setData(pageId, item);
            this.pageLoading();
            this.Request('switchShop', { //切换店铺
                id: item.shopInfo.id
            }).then(res => {
                this.searchShop = '';
                this.toIndex('from=selectShop&status=selectShop');
                this.closePageLoading();
            }).catch(res => {
                if (res) {}
            })
        },
        reLogin() { //切换登录账号
            this.Cacher.setData(pageId, {
                from: pageId
            });
            uni.reLaunch({
                url: '../../pages/login/index?from=selectShop'
            })
        },
        toIndex(info) {
            uni.reLaunch({
                url: '../../pages/index/index?' + info
            })
        },
        checkUserInfo() {
            this.Dialog.confirm({
                title: '绑定微信',
                message: '绑定后，可使用微信快捷登录您的管理员账号',
                confirmButtonText: '立即绑定'
            }).then(() => {
                uni.login({
                    provider: 'weixin',
                    success: function(loginRes) {
                        // 获取用户信息
                        uni.getUserInfo({
                            provider: 'weixin',
                            success: function(infoRes) {
                                console.log('用户昵称为：' + infoRes.userInfo.nickName);
                            }
                        });
                    }
                });
            }).catch(() => {});
        },
        toSearch() {
            DataGo = {
                go: 'searchShop'
            }
            uni.navigateTo({
                url: './searchShop'
            })
        },
        initPage() {
            if (DataGo.go) {
                DataGo = this.Cacher.getData(DataGo.go);
                this.shops = []; //清空列表
                ajaxIndex = 1; //请求页码初始化
                this.LoadingType = 0; //加载更多提示，0加载更多 1已经全部
                if (DataGo.from == 'searchShop') { //搜索店铺页传参
                    this.searchShop = DataGo.value;
                } else {
                    this.searchShop = '';
                }
            }
            if (!requesting) {
                requesting = true; //函数节流
                this.requesting = requesting;
                this.pageLoading();
                DataFrom = this.Cacher.getData(pageId);
                this.Request('shoplist', {
                    pageSize: 20,
                    page: ajaxIndex,
                    keywords: this.searchShop
                }).then(res => {
                    requesting = false;
                    this.requesting = requesting;
                    if (res.error == 0) {
                        ajaxIndex++;
                        this.shops = this.shops.concat(getShops(res.list));
                        this.shopNum = res.count;
                        this.totalShop = res.total;
                        if (res.total === 0) { //没有任何店铺
                            this.LoadingType = 1; //
                        }
                        if (this.shops.length == 1 && DataFrom.from != 'home') { //只有一个合格的店铺就直接跳转首页；如果是从首页跳转的就不必
                            let shop = this.shops[0]; 
                            this.Cacher.setData(pageId, {
                                from: pageId,
                                shopInfo: shop.shopInfo,
                                totalShops:res.total
                            });
                            this.Request('switchShop', {
                                id: shop.shopInfo.id
                            }).then(res => {
                                this.toIndex('from=selectShop&status=onlyOne')
                            })
                        } else {
                            // this.checkUserInfo()
                        }
                    } else {
                        this.Toast(res.message)
                    }
                    this.closePageLoading();
                }).catch(res => {
                    requesting = false;
                    this.requesting = requesting;
                    this.Toast(res.message);
                })
            }
        }
    },
    onPullDownRefresh() {
        requesting = false;
        this.requesting = requesting;
        this.shops = []; //清空列表
        ajaxIndex = 1; //请求页码初始化
        this.LoadingType = 0; //加载更多提示，0加载更多 1已经全部
        this.initPage();
    },
    onReachBottom() {
        if (this.shops.length < this.shopNum) {
            this.initPage();
            this.LoadingType = 0;
        } else {
            this.LoadingType = 1;
        }
    },
    onShow() {
        this.initPage();
    },
    onUnload() {
        requesting = false;
        this.requesting = requesting;
        this.shops = []; //清空列表
        ajaxIndex = 1; //请求页码初始化
        this.LoadingType = 0; //加载更多提示，0加载更多 1已经全部 
    },
    onLoad(option) {
        DataFrom = this.Cacher.getData(option.from); //
        DataGo = {
            go: ''
        }; 
    }
}
